@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&family=Outfit:wght@200&family=Roboto:wght@100&display=swap');
*{
  box-sizing: border-box;
  margin: 0%;
  padding: 0%;

}

body {
  margin: 0;
  font-family: 'Outfit', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p{
  font-size: 1.6rem;
}

h1{
  font-size: 6rem;
}

.app{
  width: 100%;
  height: 100vh;
  position: relative;
  background-color: rgba(0,0,0, 0.4);
  color: #fff;
}

.app::before{
  content: '';
  background: url('./assets/deer.jpg') no-repeat center center/cover;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0%;
  left: 0%;
  z-index: -1;
}

.container{
  max-width: 700px;
  height: 600px;
  margin: auto;
  padding: 0 1rem;
  position: relative;
  top: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.app .search{
  text-align: center;
  padding: 1rem;
}

.app input {
padding: 0.7rem 0.5rem;
font-size: 0.9rem;
border-radius: 25px;
border: 1px solid rgba(255, 255,255, 0.8);
background: rgba(255, 255,255, 0.1);
color: #fff;
}

::placeholder{
  color: #fff;
  
}

.app .top{
  width: 100%;
  margin: 1rem auto;
}

.app .description{
  position: relative;
  right: -90%;
  transform-origin: 0 0;
  transform: rotate(267deg)
}

.app .bottom{
  width: 100%;
  display: flex;
  flex-direction: row;
  position: relative;
  justify-content: space-between;
  margin: 1rem auto;
  padding: 1rem;
  border-radius: 10px;
  background-color: rgba(255, 255,255, 0.2);
}

.bold{
  font-weight: 700;
}
